<template>
  <div class="flex flex-col">
    <div class=" m-2 flex justify-between flex-wrap">
      <div class="flex flex-col">
        <input
          ref="txtdni"
          type="number"
          class=" p-3 bg-gray-100 rounded-xl"
          placeholder="Dni"
          maxlength="8"
          v-model="dni"
          @input="get_list()"
          @keyup.enter="get_list()"
        />
      </div>
    </div>

    <div class="flex">
      <vs-button @click="verCredenciales = !verCredenciales">
        Ver resumen de Credenciales
      </vs-button>

      <vs-button
        @click="
          verExamen = true;
          watch_reloj();
        "
      >
        Ver resumen entrada al examen
      </vs-button>
    </div>

    <div v-if="verCredenciales" class=" flex flex-col p-2 bg-gray-100">
      <div class=" flex">
        <button
          @click="getResumenCredenciales()"
          class="px-1 text-xs mr-2 focus:outline-none rounded-xl bg-gray-500 text-white"
        >
          Actualizar
        </button>

        <h1 class=" text-lg font-medium">
          Resumen de descargas de Credenciales {{ getTotalCredenciales }} /
          {{ getTotalCredencialesTotales }}
        </h1>
      </div>

      <div class=" flex flex-col">
        <div
          class=" rounded-3xl my-2 flex flex-col text-left"
          v-for="(item, i) in listaDownloadCredenciales"
          :key="i"
        >
          <span class="text-sm font-medium">
            {{ item.nombre }}:
            <b class="text-sm px-1 bg-green-500 text-white"
              >{{ item.count }} / {{ item.countToTal }}</b
            ></span
          >
        </div>
      </div>
    </div>

    <div class="text-center flex flex-col lg:flex-row lg:flex-wrap">
      <div
        class=" relative flex flex-col mx-2 border p-3 rounded-lg mt-2 lg:mt-0"
        v-for="(item, i) in dataList"
        :key="i"
      >
        <div
          class=" flex flex-col text-left text-xs w-full"
          style="width: 240px !important;"
        >
          <div class=" mt-5 border border-black  p-2 flex flex-col">
            <span
              class=" text-lg text-center font-medium uppercase cursor-pointer"
              >{{ `${item.nombre}` }}</span
            >

            <div class="h-px bg-black"></div>

            <span class=" font-medium mt-2">
              <i class="fas fa-user text-green-500 mr-2"></i>
              {{ item.dni }}</span
            >

            <span class=" flex flex-col"
              >Modalidad:
              <span class=" font-bold text-lg">{{ item.modalidad }}</span></span
            >

            <div class=" flex justify-between">
              <span
                >Aula:
                <span class=" font-bold text-xl">{{ item.aula }}</span></span
              >

              <span
                >Asiento:
                <span class=" font-bold text-xl">{{ item.asiento }}</span></span
              >
            </div>

            <div
              class="flex justify-center items-center text-center mt-2 cursor-pointer"
            >
              <img :src="`${base_photo}${item.photo}`" class="foto" />
            </div>

            <div class=" w-full mt-3">
              <vs-button
                v-if="!item.is_register"
                danger
                class=" focus:outline-none w-full"
                @click="
                  item_selected = item;
                  dialogConfirm = true;
                "
              >
                Marcar Ingreso
              </vs-button>

              <div
                class=" rounded-3xl bg-green-500 text-center p-2 text-white w-full"
                v-if="item.is_register"
              >
                {{ item.fecha_ingreso }}
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <vs-dialog
      width="550px"
      not-center
      blur
      v-model="dialogConfirm"
      class=" z-50"
    >
      <template #header>
        <h4 class="not-margin">Registrar <b>Postulante</b></h4>
      </template>

      <div class="con-content">
        <p>Esta seguro que desea registar al postulante? <br /></p>
      </div>

      <template #footer>
        <div class="flex justify-between">
          <div></div>
          <div class="flex">
            <vs-button
              id="btn_aceptar"
              autofocus
              @click="marcar_ingreso()"
              transparent
            >
              Si
            </vs-button>
            <vs-button @click="dialogConfirm = false" dark transparent>
              Cancelar
            </vs-button>
          </div>
        </div>
      </template>
    </vs-dialog>

    <vs-dialog blur v-model="verExamen" full-screen>
      <template #header>
        <h4 class="not-margin">Datos en Tiempo Real</h4>
      </template>

      <div class="con-content">
        <div>
          <div v-for="(local, i) in localList" :key="i" class="my-2">
            <b
              class=" text-xl cursor-pointer my-2"
              @click="localSelected = local"
              >{{ i + 1 }}.- {{ local.nombre }}
            </b>

            <b class="text-xl rounded-xl text-blue-500 p-1  ">
              {{ getTotalPorLocalConteo(local.id) }} /
              {{ getTotalPorLocal(local.id) }}
            </b>
          </div>
        </div>

        <div
          v-if="localSelected"
          class="flex flex-col p-2 bg-gray-100 mt-5 text-xs"
        >
          <b class="text-blue-500 text-lg">{{ localSelected.nombre }}</b>

          <div
            class="flex flex-wrap w-1/full h-96 lg:h-full overflow-scroll lg:overflow-hidden"
          >
            <ul
              class="flex flex-wrap mx-2"
              v-for="(item2, ii) in getAulasByLocal(localSelected.id)"
              :key="ii"
            >
              <li class="my-3 bg-gray-200 p-2 rounded-xl">
                <span
                  ><b class="font-bold text-lg">{{ item2.aula }}</b></span
                >
                <span
                  class="p-1 text-lg text-white rounded-lg ml-2"
                  :class="
                    item2.count >= item2.total_asientos
                      ? 'bg-green-500'
                      : 'bg-red-400'
                  "
                >
                  <b> {{ item2.count }}</b> /
                  <b>{{ item2.total_asientos }}</b>
                </span>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </vs-dialog>
  </div>
</template>

<script>
import axios from "@//plugins//axios.js";
export default {
  data() {
    return {
      dni: "",
      dataList: [],
      dialogConfirm: false,
      item_selected: {},
      listaDownloadCredenciales: [],
      ver_resumen: false,
      localList: [],
      pabellonList: [],
      aulastotalesList: [],
      dialogTiempoReal: false,
      verCredenciales: false,
      verExamen: false,
      localSelected: null,
    };
  },

  computed: {
    base_photo() {
      let host =
        process.env.NODE_ENV == "development"
          ? process.env.VUE_APP_BASE_URL
          : process.env.VUE_APP_URL;

      host = host.replace("/api/v1/", "/media/");
      return host;
    },

    getTotalCredenciales() {
      if (this.listaDownloadCredenciales.length <= 0) return 0;
      let total = 0;
      this.listaDownloadCredenciales.forEach((element) => {
        total += element.count;
      });

      return total;
    },

    getTotalCredencialesTotales() {
      if (this.listaDownloadCredenciales.length <= 0) return 0;
      let total = 0;
      this.listaDownloadCredenciales.forEach((element) => {
        total += element.countToTal;
      });

      return total;
    },
  },

  watch: {
    verExamen(newValue) {
      if (!newValue) {
        console.log("saliendo");
      }
    },

    dialogConfirm(newValue) {
      if (newValue) {
        setTimeout(() => {
          let boton = document.getElementById("btn_aceptar");
          boton.focus();
        }, 100);
      }
    },

    ver_resumen(newValue) {
      if (newValue) {
        this.getResumenCredenciales();
      }
    },
  },

  mounted() {
    this.get_list();
    this.getResumenCredenciales();

    setTimeout(() => {
      this.$refs.txtdni.focus();
    }, 10);

    this.getLocales();
    this.getPabellones();

    // this.watch_reloj();
  },

  methods: {
    getTotalPorLocalConteo(localId) {
      const data = this.aulastotalesList.filter(
        (item) => item.localId == localId
      );

      let total = 0;
      data.forEach((element) => {
        total += element.count;
      });

      return total;
    },

    getTotalPorLocal(localId) {
      const data = this.aulastotalesList.filter(
        (item) => item.localId == localId
      );

      let total = 0;
      data.forEach((element) => {
        total += element.total_asientos;
      });

      return total;
    },

    getAulasByLocal(locald) {
      const aulas = this.aulastotalesList.filter(
        (item) => item.localId == locald
      );

      return aulas;
    },

    async getLocales() {
      const { data } = await axios.get("local");
      this.localList = data;
    },

    async getPabellones() {
      const { data } = await axios.get("pabellon");
      this.pabellonList = data;
    },

    async getResumenAulas() {
      const { data } = await axios.get("get_resumen_aulas");
      this.aulastotalesList = data;
    },

    getResumenCredenciales() {
      axios.get("getResumenCredenciales").then((resp) => {
        this.listaDownloadCredenciales = resp.data;
      });
    },

    get_list() {
      try {
        const dni = this.dni;
        // if dni > 8 digitos, dejar solo los primeros 8 digitos
        if (dni.length > 8) {
          this.dni = dni.substring(0, 8);
        }

        if (dni.length == 0 || dni.length >= 8) {
          axios
            .get("get_list_postulante_ingresos", {
              params: {
                dni,
              },
            })
            .then((resp) => {
              this.dataList = resp.data;

              if (this.dataList.length == 1) {
                this.item_selected = this.dataList[0];
                this.marcar_ingreso();
                this.dni = "";
                setTimeout(() => {
                  this.$refs.txtdni.focus();
                }, 100);
              }
              this.getResumenCredenciales();
            });
        }
      } catch (error) {
        //
      }
    },

    async watch_reloj() {
      try {
        await axios.get("watch_reloj");
        this.getResumenAulas();

        if (this.verExamen) {
          setTimeout(() => {
            console.log("consultando...");
            this.watch_reloj();
          }, 1000);
        }
      } catch (error) {
        //
      }
    },

    async marcar_ingreso() {
      if (this.dni.length <= 0) return;

      const loading = this.$vs.loading();
      try {
        let obj = {
          id: this.item_selected.id,
        };
        const { data } = await axios.post("marcar_ingreso_by_id/", obj);

        if (data.length <= 0) {
          this.$vs.notification({
            title: "Examen",
            text: "Ocurrio un problema al registrar al postulante! ",
            color: "warning",
          });
          return;
        }

        const registro = data[0];

        const index = this.dataList.findIndex((item) => item.id == registro.id);

        if (index > -1) {
          this.dataList[index] = registro;

          this.$vs.notification({
            title: "Examen",
            text: "El postulante fu registrado correctamente! ",
            color: "success",
          });
        } else {
          this.$vs.notification({
            title: "Examen",
            text: "No se encontr el registro! ",
            color: "warning",
          });
        }

        this.getResumenCredenciales();
      } catch (error) {
        //
        this.$vs.notification({
          title: "Examen",
          text: "Ocurrio un error al registrar al postulante! ",
          color: "danger",
        });
      } finally {
        loading.close();
        this.dialogConfirm = false;
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.foto {
  width: 240px !important;
  height: 288px !important;
}
</style>
